import {Component, OnInit} from '@angular/core';
import {AuthService} from '../services/auth.service';
import {NewsService} from '../services/news.service';
import {ActivatedRoute} from '@angular/router';
import {News} from '../models/news.model';
import {NewPostDialogComponent} from './dialogs/new.post.dialog.component';
import {MatDialog, MatDialogConfig, MatDialogRef} from '@angular/material/dialog';

@Component({
	selector: 'app-home',
	templateUrl: './news.component.html',
	styleUrls: ['./news.component.css']
})
export class NewsComponent implements OnInit {
	public news: Array<News> = [];
	public loading = true;
	private queryObj = {
		skip: 0,
		limit: 5
	};
	public error: string;

	constructor(public _authService: AuthService,
				private _newsService: NewsService,
				private route: ActivatedRoute,
				private dialog: MatDialog) {
	}

	openDialog(type: String, news: News = null) {
		const config: MatDialogConfig = {
			disableClose: true,
			width: '',
			height: '80%',
			position: {
				top: '',
				bottom: '',
				left: '',
				right: ''
			},
			data: {
				type: type,
				news: news
			}
		};

		let dialogRef = this.dialog.open(NewPostDialogComponent, config);
		dialogRef.afterClosed().subscribe(result => {
			if (result) {
				const index = this.news.findIndex(a => a._id === result._id);

				if (index > -1) {
					this.news.splice(index, 1, result);
				} else {
					this.news.unshift(result);
				}
			}
		});
	}

	ngOnInit() {
		if (!this.route.snapshot.params['id']) {
			return this.getNews();
		}

		if (this.route.snapshot.data.article) {
			this.news.push(this.route.snapshot.data.article);
			this.loading = false;
		} else {
			this.error = 'I\'m sorry, something has gone wrong, please contact us regarding this issue.'
		}
	}

	getNews(): void {
		this._newsService.getNews(this.queryObj)
			.subscribe((news: Array<News>) => {
				this.news = this.news.concat(news);
				this.loading = false;
			});
	}

	delete(news: News) {

		let dialogRef = this.dialog.open(ConfirmationDialog);
		dialogRef.afterClosed().subscribe(result => {
			if (result === true) {
				news.deleting = true;

				this._newsService.deleteNews(news)
					.then(() => this.news = this.news.filter(a => a._id !== news._id))
					.catch((err) => console.log(err));
			}
		});
	}

	inView(index: number, event): void {
		if (event.value && !this.loading) {
			if (index >= this.news.length - 1 && this.news.length === this.queryObj.skip + this.queryObj.limit) {
				this.loading = true;
				this.queryObj.skip += this.queryObj.limit;

				this.getNews();
			}
		}
	}
}

@Component({
	selector: 'dialog-result-example-dialog',
	template: '<h1 mat-dialog-title>Deleting article</h1>' +
	'<div mat-dialog-content>Are you sure?</div>' +
	'<div mat-dialog-actions>' +
	'  <button class="waves-effect waves-light btn orange" [mat-dialog-close]="false" style="margin-right: 0.5em;">Cancel</button>' +
	'  <button class="waves-effect waves-light btn green" mat-button [mat-dialog-close]="true">Confirm</button>' +
	'</div>',
})
export class ConfirmationDialog {
	constructor(public dialogRef: MatDialogRef<ConfirmationDialog>) {
	}
}
