import {AfterViewInit, Component, Inject} from '@angular/core';
import {NewsService} from '../../services/news.service';
import {News} from '../../models/news.model';
import {environment} from '../../../environments/environment';
import {AuthService} from '../../services/auth.service';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';

declare let $: any;

@Component({
	selector: 'app-new-post-dialog',
	templateUrl: './new.post.dialog.html',
})
export class NewPostDialogComponent implements AfterViewInit {
	public options: Object = {
		placeholderText: 'Edit Your Content Here!',
		charCounterCount: false,
		immediateAngularModelUpdate: true,
		imageUploadURL: environment.urls.rest + 'api/upload',
	};
	public newPost = {
		editorContent: null,
		title: null,
		createdAt: null,
		date: null,
		dateString: null
	};

	public saving = false;

	constructor(public dialogRef: MatDialogRef<NewPostDialogComponent>,
				@Inject(MAT_DIALOG_DATA) public data: any,
				private _newsService: NewsService, private _authService: AuthService) {

		this.options['requestHeaders'] = {
			Authorization: this._authService.token
		};

		if (data.news) {
			const date = new Date(data.news._date);

			this.newPost.editorContent = data.news._content;
			this.newPost.title = data.news._title;
			this.newPost.date = data.news._date;
			this.newPost.dateString = date.toDateString();
		}
	}

	ngAfterViewInit() {
		$('.datepicker').pickadate({
			closeOnSelect: true,
			onSet: (value) => {
				let date = new Date(value.select);
				date.setHours(5);

				this.newPost.date = date;
				this.newPost.dateString = date.toDateString();
			}
		});

		$('#editor')
			.on('froalaEditor.image.removed', (e, editor, $img) => {
				$img.map((index, img) => {
					let startPos = img.src.search('/upload');
					let imgName = img.src.substr(startPos + 1);

					this._newsService.deleteImage(imgName);
				});
			});
	}

	save(): void {
		this.saving = true;
		this.newPost.createdAt = new Date();

		if (this.data.news) { // editMode
			this._newsService.editNews(this.data.news._id, this.newPost)
				.then((res) => {
					this.saving = false;
					this.dialogRef.close(new News(res));
				})
				.catch((err) => {
					this.saving = false;
					console.log(err);
				});
		} else { // create mode
			this._newsService.postNews(this.newPost)
				.then((res) => {
					this.saving = false;
					this.dialogRef.close(new News(res));
				})
				.catch((err) => {
					this.saving = false;
					console.log(err);
				});
		}
	}
}
